.g-dialog-container{
	position:fixed;
	left:0;
	top:0;
	width:100%;
	height:100%;
	background-color:rgba(0,0,0,.6);
	z-index:10000;
	/*
	display:box;
	box-pack:center;
	box-align:center;
	*/
	display:flex;
	justify-content:center;
	align-items:center;

	.dialog-window{
		padding:1em;
		border-radius:10px;
		background-color:rgba(0,0,0,0.8);

		transition:all .3s cubic-bezier(0.2, -0.22, 0.62, 1.24);

		.dialog-header{
			width:50px;
			height:50px;
			margin:0 auto;
			line-height:50px;
			text-align:center;
			font-size:40px;
			color:#fff;
			/*
			background:url("alert-icons_2px.png") no-repeat 0 0;
			background-size:50px;

			&.ok{
				background-position:0 0;
			}
			&.warning{
				background-position:0 bottom;
			}
			&.waiting{
				background:url("loading.gif") no-repeat 0 0;
				background-size:50px;
			}
			*/
		}
		.dialog-content{
			padding:1em 1em 0;
			color:#fff;
			line-height:180%;
			text-align:center;
		}
		.dialog-footer{
			padding:1em 1em 0;
			display:flex;
			justify-content:center;
			align-items:center;
			button{
				appearance:normal;
				background-color:rgba(255,255,255,0.8);
				padding:0.8em 1.8em;
				border:none;
				color:#eee;
				border-radius:5px;
				margin:0 .3em;
				text-shadow:#666 1px 1px 0;
				&.green{
					background-color:rgba(2,202,200,0.8);
				}
				&.red{
					background-color:rgba(251,23,50,0.8);
				}
			}
		}
	}
}